import './App.css';
import Axios from "axios";
import {useEffect, useState} from "react";


function App() {

  const [repo, setRepo] = useState([])
  function getRepo()
  {
    Axios.get("http://localhost/api/get").then((response)=> {
      console.log(response);
      const myRepo = response.data;
      setRepo(myRepo);
    });
  };

  useEffect(() => getRepo(),[])
  return (
    <div className="App">
      <h1>Hello world</h1>
      {repo.map((repos) => (
          <div>
          <h1>{repos.name}</h1>
        <h1>{repos.model}</h1>
        <h1>{repos.year}</h1>
        <h1>{repos.id}</h1>
          </div>
          )

      )}
    </div>
  );
}

export default App;
